import React,{useState,useEffect} from 'react';
import './App.css';
import Header from './components/Header'
import Recipe from './components/Recipe'
import Axios from 'axios'  //request bhajta ha is module sa




function App() {

//useEffect agr ma page reload pa koi event perform karana ha tu use honga

const [search , setSearch] = useState("chicken");
const [recipes,setRecipes] = useState([]);
const APPID = "9a84391d";
const APPKEY = "e1c9ced738d9dafee2406a114759fb83"
  useEffect(()=>{

    getRecipies();
  },[]); //[] ya isliya pass kiya warna infinite chala ga


  const getRecipies = async () =>{

    const response = await Axios.get(`https://api.edamam.com/search?q=${search}&app_id=${APPID}&app_key=${APPKEY}`)
  
    console.log('init....')
    console.log(response.data.hits)

    setRecipes(response.data.hits)


  }

  const onInputChange = (e)=>{

    setSearch(e.target.value)


  }

  const onSearchClick = () =>{

    getRecipies();

  }
  return (
    <div className="App">
    
    <Header search = {search} onInputChange = {onInputChange} onSearchClick = {onSearchClick} />

    {/* passing recipies data for Recipe comoponent */}

    <div className = "container">

    <Recipe recipes = {recipes}/> 
    </div>

    </div>
  );
}

export default App;
