import React from "react";

const Header =(props)=>{

    const {search , onInputChange , onSearchClick} = props

    return (
    
    <div className = "jumbotron">

        <h1 className = "display-1"><span class="material-icons brand-icon">fastfood</span>Food Recipe</h1>

        <div class="input-group mb-3 w-50 mx-auto">
  <input placeholder = "Search Your Favourite Recipe ........" type="text" value = {search} onChange = {onInputChange} class="form-control"  aria-label="Recipient's username" aria-describedby="basic-addon2"/>
  <div class="input-group-append">
    <button onClick = {onSearchClick} className = "btn btn-dark">Search Recipe</button>
  </div>
</div>
    </div>

    );
}

export default Header;